import { Component } from '@angular/core'
import {Validators, FormBuilder, FormGroup } from '@angular/forms'
import { RecipeService } from '../../app/recipe.service'

@Component({
  templateUrl: 'edit.html'
})
export class EditPage {
  private recipe : FormGroup;
  recipes: RecipeService

  constructor( private formBuilder: FormBuilder, rs: RecipeService) {
    this.recipes = rs
    this.recipe = this.formBuilder.group({
      name: ['', Validators.required],
      imageUrl: [''],
      prepTime: [''],
      cookTime: [''],
      servings: [''],
    });
  }
  saveForm(){
    console.log(this.recipe.value)
    this.recipes.save(this.recipe.value).catch(console.log)
  }
}



